<template>
  <div class="cardBag">
    <!-- 头部 -->
    <mt-header title="卡包"> </mt-header>
    <!-- 我的收藏 -->
    <div class="myCardBagBox">
      <el-row>
        <el-col :span="24" v-for="(item, index) in goodsList" :key="index">
          <el-row
            class="goods"
            @click.native="goToGoodsDetails(item.id)"
            @touchmove.native.once="deleteCardBag(item.id, index)"
          >
            <div>
              <el-col :span="6">
                <div class="block">
                  <el-image :src="item.mainPic" fit="cover"></el-image>
                </div>
              </el-col>
              <el-col :span="18">
                <el-row>
                  <div class="goodstTile">
                    <el-col :span="24"
                      ><div class="goodsBox goodsTitleFont">
                        {{ item.dtitle }}
                      </div></el-col
                    >
                  </div>

                  <div class="goodsBox goodsPrice">
                    <el-col :span="12"
                      ><h6
                        style="font-size: 12px;  font-weight: normal;  color: #8f8f94; text-align:left;"
                      >
                        原价:{{ item.originalPrice }}
                      </h6>

                      <h6 style="color:red;text-align:left;padding-top: 10px;">
                        券后 ￥
                        <span style="font-size:18px">{{
                          item.actualPrice
                        }}</span>
                      </h6>
                    </el-col>
                  </div>
                  <div class="goodsBox goodsMonthSales">
                    <el-col :span="12">
                      <h6
                        style="font-size: 12px;font-weight: normal; color: #8f8f94;text-align:right;"
                      >
                        <div>
                          <el-tag type="danger" size="mini" effect="plain">
                            <span>{{ item.couponPrice }}元券</span>
                          </el-tag>
                        </div>
                      </h6>
                      <div>
                        <!-- <h6 style="font-size: 12px;font-weight: normal; color: #8f8f94;text-align:right; padding-top: 10px;" >
                          已售: {{ item.monthSales / 10000 }} 万件
                        </h6> -->

                        <h6
                          style="font-size: 12px;font-weight: normal; color: #8f8f94;text-align:right; padding-top: 10px;"
                          v-if="item.monthSales > 1000"
                        >
                          已售:{{ (item.monthSales / 10000).toFixed(2) }} 万
                        </h6>
                        <h6
                          style="font-size: 12px;font-weight: normal; color: #8f8f94;text-align:right; padding-top: 10px;"
                          v-if="item.monthSales < 1000"
                        >
                          已售:{{ item.monthSales }} 件
                        </h6>
                      </div></el-col
                    >
                  </div>
                </el-row>
              </el-col>
            </div>
          </el-row>
        </el-col>
      </el-row>
    </div>

    <!-- 精选 -->
    <!-- 商品列表栏 -->
    <!-- <div class="goodsList-container">
        <GoodsList :goodsList="goodsList" @nextPage="getNextPage"></GoodsList>
      </div> -->
  </div>
</template>

<script>
import { Toast } from "mint-ui";
export default {
  data() {
    return {
      user: this.$store.getters.getUser,
      goodsList: null
    };
  },
  created: function() {
    this.openPersonal();
    this.getMyCardBag();
  },
  methods: {
    openPersonal() {
      //当打开页面vuex中没有user时候 执行登录页面造作 跳转到登录页面
      // console.log(this.$store.getters.getUser);
      if (
        this.$store.getters.getUser == null ||
        this.$store.getters.getUser == ""
      ) {
        //console.log("跳转登录");
        this.$router.push({ name: "Login" });
      }
    },
    getMyCardBag() {
      if (this.user != null) {
        //执行获取操作
        this.$http
          .get("goodsCardBag/myCardBag.action?userId=" + this.user.userId)
          .then(function(res) {
            var result = JSON.parse(JSON.stringify(res.body.result));
            if (result.code == 200) {
              this.goodsList = result.data;
            }else{
              Toast({
                message: result.msg,
                // iconClass: 'el-icon-info',
                position: "center",
                duration: 2000
              });
            }
            // console.log(this.goodsList);
          });
      } else {
        //执行不获取操作
      }
    },
    // 跳转商品详情页
    goToGoodsDetails(id) {
      // this.$router.push({ name: 'GoodsDetails', params: { id: id }})
      this.$router.push({ path: `/GoodsDetails/${id}` });
    },
    deleteCardBag(id, index) {
      this.$confirm("此操作将删除该商品卡包, 是否继续?", "提示", {
        customClass: "message-deleteCardBag",
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          //console.log("delete")
          //执行取消收藏
          var collction = {
            userId: this.user.userId,
            goodsId: id
          };
          this.$http
            .post("goodsCardBag/deleteCardBag.action", collction)
            .then(function(res) {
              var result = JSON.parse(JSON.stringify(res.body.result));
              //取消收藏成功
              if (result.code == 200) {
                Toast({
                  message: "取消收藏成功",
                  // iconClass: 'el-icon-info',
                  position: "center",
                  duration: 1000
                });
                //设置取消收藏成功成功
                this.goodsList.splice(index, 1);
                // array.splice(array.findIndex(item => item === 2), 1);
              } else {
                //取消收藏成功失败
                Toast({
                  message: result.msg,
                  // iconClass: 'el-icon-info',
                  position: "center",
                  duration: 1000
                });
              }
            });
        })
        .catch(() => {
          Toast({
            message: "取消刪除",
            // iconClass: 'el-icon-info',
            position: "center",
            duration: 1000
          });
        });
    }
  }
};
</script>

<style sscoped>
.goodsTitleFont {
  font-size: 14px;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "\5FAE\8F6F\96C5\9ED1", Arial, sans-serif;
  text-align: left;
}
.goodsBox {
  margin: 10px 20px;
  white-space: nowrap;
}
.goods {
  margin: 5px 5px;
  background-color: white;
  border-radius: 5px;
}
h6 {
  margin: 15px auto;
}
.goodsList-container {
  position: absolute;
  margin-top: 10px;
}

.message-deleteCardBag {
  width: auto !important;
}
</style>
